import React from "react";

class detail extends React.Component{
    //redirecting to home if trying to manually accessing /movie-detail. Can only do this by clicking links.
    componentDidMount(props){
        //getting location and history from props. console.log(props) to see whats in the props.
        const {location, history} = this.props;
        if(location.state === undefined){
            history.push("/");
        }
    }
    render(){
        const {location} = this.props;
        if(location.state){
            return <span>{location.state.title}</span>;
        } else{
            return null;
        }
        
    }
}

export default detail;